@import url('https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap');
*{
  font-family: 'Poppins', sans-serif;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-size: large;
}
.App{
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  color: black;
  background-color: aliceblue;
}
.generator{
  width: 350px;
  padding: 30px 20px;
  border-radius: 20px;
  background-color: azure;
}
.generator_title,.password{
  text-align: center;
  margin-bottom: 20px;
}
.generator_form-control{
  display: flex;
  justify-content: space-between;
  margin-bottom: 10px;
}
.generator_form-control input{
  accent-color: purple;
}
.generator_length{
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin: 30px 0;
  padding: 15px;
  border-radius: 5px;
  background-color: aliceblue;
}
.generator_length-counter button{
  width: 35px;
  height: 35px;
  font-size: 20px;
  margin:  0 20px;
  color: #fff;

  background-color: black;
  border: 1pc solid black;
  border-radius: 5px;
  transition: all 200ms linear;

}
.generator_length-counter button:hover{
  color: black;
  background-color:white ;
  border: 1px solid black;

}
.generator_length-counter span{
  display: inline-block;
  font-size: 18px;
  width: 36px;
  text-align: center;
}
.generator_form-actions{
  display: flex;
  justify-content: space-around;
}
.generator_form-action .btn{
  padding: 8px 12px;
  color: black;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}
.generator_form-action .generate-btn{
  background: linear-gradient(to right,aliceblue,purple);
}

.generator_form-action .copy-btn{
  background: linear-gradient(to right,lightgreen,lightblue);
}